import { ContactComponent } from './contact/contact.component';
import { RestmongoComponent } from './restmongo/restmongo.component';
import { CountryLookupComponent } from './country-lookup/country-lookup.component';
import { DatashareReceiverComponent } from './datashare-receiver/datashare-receiver.component';
import { DatashareSenderComponent } from './datashare-sender/datashare-sender.component';
import { Dataflow1Component } from './dataflow1/dataflow1.component';
import { ProductsComponent } from './products/products.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StartComponent } from './start/start.component';

const routes: Routes = [
  { path: '', component: StartComponent, pathMatch: 'full' },
  { path: 'products/:id', component: ProductsComponent},
  { path: 'products', component: ProductsComponent },
  { path: 'dataflow1', component: Dataflow1Component },
  { path: 'datashareSender', component: DatashareSenderComponent },
  { path: 'datashareReceiver', component: DatashareReceiverComponent },
  { path: 'countryLookup', component: CountryLookupComponent },
  { path: 'restmongo', component: RestmongoComponent },
  { path: 'contact', component: ContactComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
