import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { DataService } from '../shared/data.service';

// https://rapidapi.com/apilayernet/api/rest-countries-v1?endpoint=53aa5a09e4b051a76d241368

@Component({
  selector: 'app-country-lookup',
  templateUrl: './country-lookup.component.html',
  styles: []
})
export class CountryLookupComponent implements OnInit {

  private countriesObservableAllCountries: Observable<any>;
  private countriesObservableSingleCountryID: Observable<any>;
  lookupId: string;
  singleCountryName: string;
  singleCountryRegion: string;
  singleCountryBorders: [];

  constructor(private dataService: DataService) {
    this.countriesObservableAllCountries = this.dataService.get_allCountries();
  }

  ngOnInit() {  }

  searchCountryById(lookupId) {
    this.countriesObservableSingleCountryID = this.dataService.searchCountryById(lookupId);
    this.countriesObservableSingleCountryID.subscribe( value => {
      this.singleCountryName = value[0].name;
      this.singleCountryRegion = value[0].region;
      this.singleCountryBorders = value[0].borders;
      console.log(value[0]);
    });
  }

}
