import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dataflow2',
  templateUrl: './dataflow2.component.html',
  // styleUrls: ['./dataflow2.component.css']
})
export class Dataflow2Component implements OnInit {
  @Input() text: string;
  @Output() changed = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  onChange(value) {
    this.changed.emit(value);
    console.log('event received from level 2 at level 1: ' + value);
  }

}
