import { Component, OnInit } from '@angular/core';
import { DatashareService } from '../shared/datashare.service';

@Component({
  selector: 'app-datashare-receiver',
  // templateUrl: './datashare-receiver.component.html',
  template: ` Message received: {{message}} `,
  // styleUrls: ['./datashare-receiver.component.css']
})
export class DatashareReceiverComponent implements OnInit {
  message: string;

  constructor(private sharedDataService: DatashareService) { }

  ngOnInit() {
    console.log(this.sharedDataService);
    this.sharedDataService.currentMessage.subscribe(message => {
      this.message = message;
    });
  }

}
