import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, catchError, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RestmongoService {

  constructor(private http: HttpClient) { }

  endpoint = 'http://localhost:3000/';
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  };

  private extractData(res: Response) {
    const body = res;
    return body || { };
  }

  getPosts(): Observable<any> {
    return this.http.get(this.endpoint + 'posts').pipe(
      map(this.extractData));
  }

  getPost(id): Observable<any> {
    const result = this.http.get(this.endpoint + 'posts/' + id).pipe( map(this.extractData) );
    return result;
  }

  addPost(post): Observable<any> {
    /* POSTMAN
    POST: http://localhost:3000/posts/
    BODY
    {
      "title": "post4",
    	"description": "post 4 new new new"
    }
    HEADERS: Content-Type: application/json
    */
   const result = this.http.post(this.endpoint + 'posts/', post, this.httpOptions)
   .pipe( catchError(this.handleError('addPost', post)) );
   return result;
  }

  deletePost(id) {
    /* POSTMAN
    DELETE: http://localhost:3000/posts/5d72775f7886439955b1cd59
    BODY: none
    HEADERS: Content-Type: application/json
    */
    const result = this.http.delete(this.endpoint + 'posts/' + id, this.httpOptions);
    return result;
  }

  updatePost(post, id) {
   /* POSTMAN
    PATCH: http://localhost:3000/posts/5d7242eccfc48b6761f20d95
    BODY:
    {
      "title": "post 5b",
    	"description": "adsfadsfadsfasdf"
    }
    HEADERS: Content-Type: application/json
    */
   const result = this.http.patch(this.endpoint + 'posts/' + id, post, this.httpOptions);
   result.subscribe();
   return result;
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
