import { Component, OnInit, Input } from '@angular/core';
import { RestmongoService } from '../shared/restmongo.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-restmongo',
  templateUrl: './restmongo.component.html',
  styleUrls: []
})
export class RestmongoComponent implements OnInit {
  allPosts: any;
  postData = { post_id: '', post_title: '', post_description: '', post_date: '' };
  singlePostTitle: string;
  singlePostDescription: string;
  singlePostDate: string;

  constructor(public rest: RestmongoService) {}

  ngOnInit() {
    this.updatePosts();
  }

  updatePosts() {
    this.allPosts = this.rest.getPosts();
  }

  logPosts() {
    this.rest.getPosts().subscribe( data => {
      console.log(data);
    });
  }

  getPost(id) {
    this.rest.getPost(id).subscribe((data: {}) => {
      this.singlePostTitle = data['title'];
      this.singlePostDescription = data['description'];
      this.singlePostDate = data['date'];
    });
  }

  addPost(post) {
    post = {
      title: this.postData.post_title,
      description: this.postData.post_description
    };
    this.rest.addPost(post).subscribe( () => {
      this.updatePosts();
    });
  }

  deletePost(id) {
    this.rest.deletePost(id).subscribe( (response: {}) => {
      console.log('Deleted post: ', response);
      this.updatePosts();
    });
  }

  updatePost(post, id) {
    post = {
      title: this.postData.post_title,
      description: this.postData.post_description
    };
    id = this.postData.post_id;
    console.log('postdata:', this.postData);
    this.rest.updatePost(post, id).subscribe( () => {
      this.updatePosts();
    });
  }

}
