import { Component, OnInit } from '@angular/core';
import { DatashareService } from '../shared/datashare.service';

@Component({
  selector: 'app-datashare-sender',
  template: `
    {{message}}
    <button (click)="newMessage()">New Message</button>
    `,
  // styleUrls: ['./datashare-sender.component.css']
})
export class DatashareSenderComponent implements OnInit {
  message: string;

  constructor(private sharedDataService: DatashareService) { }

  ngOnInit() {
    this.sharedDataService.currentMessage.subscribe(message => this.message = message);
  }

  newMessage() {
    this.sharedDataService.changeMessage('Hello from Sender');
  }

}
