import { Component, OnInit } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { DataService } from '../shared/data.service';
import { ActivatedRoute } from '@angular/router';
import { isNumber } from '@ng-bootstrap/ng-bootstrap/util/util';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: []
})
export class ProductsComponent implements OnInit {
  // Two different options to get Data (Families, Products)
  private families = [];
  private productsObservable: Observable<any>;
  singleProductObservable: Observable<any>;
  text: string; // from dataflow component
  singleProductName: string;
  singleProductQuantity: number;
  searchId: number;
  urlId: string;
  noResultText = 'No results found.';
  noResultTextDisplay = false;

  constructor(
    private dataService: DataService,
    private activatedRoute: ActivatedRoute) {

    this.productsObservable = this.dataService.get_products();
    this.dataService.get_families().subscribe((res: any[]) => {
      this.families = res;
    });
  }


ngOnInit() {
    this.activatedRoute.paramMap.subscribe(params => {
      this.urlId = params.get('id');
      console.log('product id:', this.urlId);
      if (this.urlId !== null) {
        console.log('getting Product infos..');
        this.searchProductById(this.urlId);
      }
    });
}

searchProductById(searchId) {
  this.singleProductObservable = this.dataService.get_SingleProduct(searchId);
  this.singleProductObservable.subscribe( response => {
    console.log('response: ', response);
    if (response.length !== 0) {
      this.singleProductName = response[0].name;
      this.singleProductQuantity = response[0].quantity;
      this.noResultTextDisplay = false;
    } else {
      this.singleProductName = null;
      this.singleProductQuantity = null;
      this.noResultTextDisplay = true;
      throw new Error('id ' + searchId + ' is not valid!!');
    }
  });
}

onLevel2Changed(value) {
    this.text = value;
    console.log('event received from level 2 to products:' + this.text);
  }

}
