import { Component, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-dataflow1',
  templateUrl: './dataflow1.component.html',
  // styleUrls: ['./dataflow1.component.css']
})
export class Dataflow1Component implements OnInit {
  @Output() text: string;

  constructor() { }

  ngOnInit() {
  }

  onChange(value) {
    this.text = value;
    console.log('level1change:' + this.text);
  }

  onLevel2Changed(value) {
    this.text = value;
    console.log('event received from level 2 to level1:' + this.text);
  }

}
