import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

/* Like this, the service doesn't need to be declared in app.module in providers sections
@Injectable({
  providedIn: 'root'
})
*/

@Injectable()

export class DataService {
  // fake json server
  baseUrlTest1 = 'http://localhost:3000';

  // real online API
  countriesBaseUrl = 'https://restcountries-v1.p.rapidapi.com';
  countriesHeaders = new HttpHeaders()
    .set('x-rapidapi-host', 'restcountries-v1.p.rapidapi.com')
    .set('x-rapidapi-key', '380625d8bamsha16e92c8cd35df1p1113a6jsn3128bf74f599');

  productId = 1;

  constructor(private httpClient: HttpClient) { }

  get_allCountries() {
    return this
      .httpClient
      .get(`${this.countriesBaseUrl}/all`, {
        headers: this.countriesHeaders
      });
  }

  searchCountryById(lookupId) {
    console.log('searchCountryById(' + lookupId + ') called.');
    return this
      .httpClient
      .get(`${this.countriesBaseUrl}/alpha/`, {
        headers: this.countriesHeaders,
        params: new HttpParams().set('codes', lookupId)
      });
  }

  get_products() {
    return this
      .httpClient
      .get(this.baseUrlTest1 + '/products');
  }

  get_families() {
    return this
      .httpClient
      .get(this.baseUrlTest1 + '/families');
  }

  get_SingleProduct(productId) {
    return this
      .httpClient
      .get(this.baseUrlTest1 + '/products?id=' + productId);
  }

}
